$COLORS: (
    white: #ffffff,
    black: #000000,
    gray: #65666b,
    link: #2193ca
);

$transparent-white: rgba(255,255,255,0.7);
$gradient-fromBottom: linear-gradient( transparent, rgba(255,255,255,1) 100%);
$gradient-fromTop: linear-gradient(rgba(255,255,255,1) 0%, transparent);
$gradient-opaque: linear-gradient(rgba(255,255,255,1) 100%, transparent);

$defaultfont: 'proxima-nova';
