html, 
body {
    font-size: 16px;
    background: map-get($COLORS, white);
    font-family: $defaultfont;
    width: 100vw;
    height: 400vh;
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    *, *:after, *:before {
        font-family: $defaultfont;
        box-sizing: border-box;
    }
}