a {
    text-decoration: none;
    color: map-get($COLORS, black);
    text-decoration: underline;
    text-decoration-color: map-get($COLORS, black);
    transition: all 0.3s ease-in-out;
    cursor: default;

    &[href] {
        cursor: pointer;

        &:hover {
            color: lighten(map-get($COLORS, black), 10%);
            text-decoration: underline;
            cursor: pointer;
        }
    }
}

p {
    @extend %currentfont;
    font-size: 15px;
    line-height: 1.5;
    color: map-get($COLORS, black);
    margin-top: 16px;
    margin-bottom: 16px;
    max-width: 42rem;
}

blockquote, q {
    @extend %currentfont;
    color: map-get($COLORS, black);
    font-size: 1.875em;
    line-height: 1.25;
    max-width: 42rem;
}

small {
    font-size: 0.8em;
}

h1 {
    @extend %currentfont;
    font-size: 4em;
    line-height: 1.25;
    font-weight: bold;
    color: map-get($COLORS, black);
    text-decoration-color: map-get($COLORS, black);

    @include bp('large') {
        font-size: 3.5em;
    }

    @include bp('medium') {
        font-size: 2.125em;
    }

    @include bp('small') {
        font-size: 1.5em;
    }
}

h2 {
    @extend %currentfont;
    font-size: 3.125em;
    line-height: 1.25;
    font-weight: bold;
    color: map-get($COLORS, black);
    text-decoration-color: map-get($COLORS, black);

    @include bp('large') {
        font-size: 2.75em;
    }

    @include bp('medium') {
        font-size: 2em;
    }

    @include bp('small') {
        font-size: 1.5em;
    }
}

h3 {
    @extend %currentfont;
    font-size: 2.25em;
    font-weight: bold;
    color: map-get($COLORS, black);
    text-decoration-color: map-get($COLORS, black);

    @include bp('large') {
        font-size: 2.25em;
    }

    @include bp('medium') {
        font-size: 1.75em;
    }

    @include bp('small') {
        font-size: 1.25em;
    }
}

h4 {
    @extend %currentfont;
    font-size: 1.5em;
    font-weight: bold;
    color: map-get($COLORS, black);
    text-decoration-color: map-get($COLORS, black);

    @include bp('small') {
        font-size: 1em;
    }
}

h5 {
    @extend %currentfont;
    font-size: 1.25em;
    color: map-get($COLORS, black);
    text-decoration-color: map-get($COLORS, black);
}

img {
    max-width: 100%;
}

ul, ol {
    padding-left: 1.25em;
    max-width: 42rem;

    li {
        @extend %currentfont;
        font-size: 14px;
        line-height: 28px;
        color: map-get($COLORS, black);
    }
}
